            <div class="m-single-meta">
            <li>
                <em class="u-label">首领</em>
                <div class="u-value u-boss-list">
                    {{ meta.fb_boss.toString() }}
                </div>
            </li>
            <li>
                <em class="u-label">模式</em>
                <div class="u-value u-mode-list c-jx3fb-mode">
                    {{ meta.fb_level.toString() }}
                </div>
